	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Egil Aarvik","date":"16 Jun 2004","slug":"/2004-06-16-egil-aarvik/","tags":"Y2004"},"html":"<p>A couple of years ago, I posted about the possibility that <a href=\"http://www.papascott.de/archives/2002/08/04/in-laws-to-royalty/\">a distant cousin was married to a member of the Norwegian royal family</a>. My great-uncle who told the story has since passed away, and among his effects was a newspaper article about <a href=\"http://www.nobel.se/peace/articles/committee/nnclist/bios/aarvik.html\">Egil Aarvig</a> (1912-1990), Norwegian politician, newspaper editor and chairman of the Norwegian Nobel Committee. My grandmother's maiden name was Aakvik, so it could very well be that we are related to the late Norwegian politician. Whether he had married into the royal family is yet unclear.</p>"},"next":{"fields":{"title":"Tip Me Not","date":"15 Jun 2004","slug":"/2004-06-15-tip-me-not/"}},"previous":{"fields":{"title":"PS: Mena Loves You","date":"16 Jun 2004","slug":"/2004-06-16-ps-mena-loves-you/"}}},{"node":{"fields":{"title":"Tip Me Not","date":"15 Jun 2004","slug":"/2004-06-15-tip-me-not/","tags":"Y2004"},"html":"<p>I joined the <a href=\"http://hermes.zeit.de/EM-Tipp/\" title=\"EM-Tippspiel\">Euro pool at zeit.de</a> (pointed to by <a href=\"http://jimmiz.blogg.de/eintrag.php?id=424\" title=\"EM-Tippspiel [jimmiz journal]\">Jim</a>, and after the first 3 days my result is so remarkable that I should post my picks here, so you have an idea of what's <em>not</em> to happen. I have one (un, eins, uno) point, and it took a herioc effort by Zidane to achieve that. Yesterday, for example, I picked Italy to win and Sweden to tie. Today I've picked the Czechs and Holland to win, so things are looking good for a couple of upsets.</p>\n<p>Meanwhile <a href=\"http://lumma.de/eintrag.php?id=323\">Nico</a> is complaining about the announcing talents of Reinhold Beckmann. They must not get ZDF in Lummaland, because there's <em>nobody</em> worse (not even Faßblinder) than Johannes B. (as in Brainless) Kerner. Meanwhile <a href=\"http://www.neilarmstrong.de/weblog/2004_06_01_archive.html#108721607705602524\">Neil</a> is going on about the wonderful German announcers, which I guess means the British have it even worse than anyone had imagined. (I personally use a face-recognition system on my TV that activates the mute button whenever Beckenbauer's face appears on the screen.)</p>"},"next":{"fields":{"title":"Pardon Me?","date":"15 Jun 2004","slug":"/2004-06-15-pardon-me/"}},"previous":{"fields":{"title":"Egil Aarvik","date":"16 Jun 2004","slug":"/2004-06-16-egil-aarvik/"}}},{"node":{"fields":{"title":"Pardon Me?","date":"15 Jun 2004","slug":"/2004-06-15-pardon-me/","tags":"Y2004"},"html":"<p>Did Holland really leave both their defense and their midfield at the hotel this evening? Did Germany really play the best 45 minutes I've ever seen them play? Germany leads Holland at halftime 1:0.</p>\n<p><strong>Update:</strong> Final 1:1. But a good result, and very entertaining to watch. Holland was lucky to tie, and Germany could well have won.</p>"},"next":{"fields":{"title":"Grill It (Like A Polaroid Picture)","date":"14 Jun 2004","slug":"/2004-06-14-grill-it-like-a-polaroid-picture/"}},"previous":{"fields":{"title":"Tip Me Not","date":"15 Jun 2004","slug":"/2004-06-15-tip-me-not/"}}}],"pathPrefix":"","first":false,"last":false,"index":465,"pageCount":1101}}

